<template>
  <div class="login-container">
    <transition name="fade">
    <div class="login-box" v-if="isBoxDisplay">
      <div class="logo">
        <img src="~assets/img/logo.png" />
      </div>
      <div class="login-info">
       <el-form :model="loginForm" ref="loginValidateForm" :rules="loginFormRules" class="login-form" label-width="60px">
         <el-form-item  label="工号" prop="jobNumber">
                 <el-input v-model="loginForm.jobNumber" placeholder="请输入您的工号" prefix-icon="el-icon-user"></el-input>
               </el-form-item>
               <el-form-item label="密码" prop="passWord">
                 <el-input type="password" v-model="loginForm.passWord"  placeholder="请输入您的密码" prefix-icon="el-icon-lock"></el-input>
               </el-form-item>
               <el-form-item class="btns">
                 <el-button type="primary" @click="login('loginValidateForm')">登录</el-button>
                 <el-button type="info" @click="restore('loginValidateForm')">重置</el-button>
               </el-form-item>
       </el-form>
      </div>
    </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "Login",
  components: {},
  mixins: [],
  props: {
    msg: {
      type: String,
      default: "Login",
    },
  },
  data: function () {
    return {
      loginForm:{
        jobNumber:'',
        passWord:''
      },
      loginFormRules:{
        jobNumber:[
          {  message: '请输入工号', trigger: 'blur' },
          { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur'}
        ],
        passWord:[
          {message: '请输入用户密码', trigger: 'blur'},
          { min: 4, max: 18, message: '长度在 4 到 18 个字符', trigger: 'blur' }
        ]
      },
      isBoxDisplay:false
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {
    this.isBoxDisplay = true;
  },
  methods: {
    login(formName){
      this.$refs[formName].validate((valid)=>{
        if(valid){
          if(this.loginForm.jobNumber=="Admin"&&this.loginForm.passWord=="123456"){
            this.$router.push({path:'/home',query: {id:1}});
            this.$message.success("登陆成功");
          }else if(this.loginForm.jobNumber=="Operator"&&this.loginForm.passWord=="123456"){
            this.$router.push({path:'/home',query: {id:2}});
            this.$message.success("登陆成功");
          }else if(this.loginForm.jobNumber=="Tester"&&this.loginForm.passWord=="123456"){
            this.$router.push({path:'/home',query: {id:3}});
            this.$message.success("登陆成功");
          }else if(true){
            this.$message.error("账户或密码输入错误！")
          }
        }
      })
    },
    restore(formName){
      this.$refs[formName].resetFields();
    }
    
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login-container {
  width: 100%;
  height: 100%;
  background-color: rgb(217, 223, 139);
  /* background-image: url('~assets/img/LogBack.jpg') ;
  background-repeat: no-repeat;
  -moz-background-size:100% 100%; background-size:100% 100%;  */
}
.login-box {
  background-color: rgb(250, 250, 250);
  width: 400px;
  height: 300px;
  border-radius: 8px;
  position: absolute;
  font-size: 13px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.6);
}
.logo {
  width: 117px;
  height: 117px;
  background-color: #fff;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  border:0px;
  box-shadow: 0 -5px 10px -2px rgba(0, 0, 0, 0.6);
}
.logo img {
  width: 60%;
  height: 60%;
  margin-top: 20%;
}
.login-info{
  position: absolute;
  top: 90px;
  width:100%;
}
.login-form{
  margin:0 30px 0 10px;
}
.btns{
    display: flex;
    margin-top: 15px;
    float:right;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 2.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
